import { useState } from "react"

const CreateModal = ({ active, setActive }) => {
    const [punctList, setPunctList] = useState(
        [
            { id: 1, title: "    Python", items: [{ id: 1.1, title: "  Python" }, { id: 1.2, title: "  Python" }, { id: 1.3, title: " Pytho" }] },
            { id: 2, title: "  ", items: [{ id: 2.1, title: "" }, { id: 2.2, title: " " }, { id: 2.3, title: " " }] },
            { id: 3, title: "  ", items: [{ id: 3.1, title: "  " }, { id: 3.2, title: " " }, { id: 3.3, title: "  " }] },
        ]
    )
    const [currentEditPunctId, setCurrentEditPunctId] = useState(0)
    const [currentEditPunctText, setCurrentEditPunctText] = useState('')
    const [prevEditPunctText, setPrevEditPunctText] = useState('')
    const [currentDragEl, setCurrentDragEl] = useState(null)
    const [isDragMain, setIsDragMain] = useState(0)
    const [dropParentId, setDropParentId] = useState(0)
    function calcNewIds(oldPunctList) {
        let newPunctList = []
        for (let i = 0; i < oldPunctList.length; i++) {
            let newItems = []
            for (let j = 0; j < oldPunctList[i].items.length; j++) {
                newItems.push({ id: (i + 1) + ((j + 1) / 10), title: oldPunctList[i].items[j].title })
            }
            newPunctList.push({ id: i + 1, title: oldPunctList[i].title, items: newItems })
        }
        return newPunctList
    }
    function addPunct() {
        let nextLenList = punctList.length + 1;
        setPunctList([...punctList, { id: nextLenList, title: "", items: [{ id: nextLenList + 0.1, title: "" }] }])
    }
    function addPodPunct(punctId) {
        for (let i = 0; i < punctList.length; i++) {
            if (punctList[i].id === punctId) {
                punctList[i].items.push({ id: (punctId) + ((punctList[i].items.length + 1) / 10), title: "" })
            }
        }
        setPunctList([...punctList])
    }
    function deletePunct(punctId) {
        let newPunctList = []
        for (let i = 0; i < punctList.length; i++) {
            if (punctList[i].id !== punctId) {
                newPunctList.push(punctList[i])
            }
        }
        newPunctList = calcNewIds(newPunctList)
        setPunctList([...newPunctList])
    }
    function deletePodPunct(punctId, podPunctId) {
        let newL = [...punctList]
        for (let i = 0; i < newL.length; i++) {
            if (newL[i].id === punctId) {
                let newPodPunctList = []
                for (let j = 0; j < newL[i].items.length; j++) {
                    if (newL[i].items[j].id !== podPunctId) {
                        newPodPunctList.push(newL[i].items[j])
                    }
                }
                newL[i].items = newPodPunctList
            }
        }
        let newPunctList = calcNewIds(newL)
        setPunctList([...newPunctList])
    }
    function editPunct(punctId, title) {
        setCurrentEditPunctId(punctId)
        setCurrentEditPunctText(title)
        setPrevEditPunctText(title)
    }
    const handleChange = event => {
        setCurrentEditPunctText(event.target.value);
    };
    function submitEdit(punctId) {
        let newPunctList = [...punctList]
        for (let i = 0; i < newPunctList.length; i++) {
            if (newPunctList[i].id === punctId) {
                newPunctList[i].title = currentEditPunctText
            }
        }
        setCurrentEditPunctId(0)
        setPunctList([...newPunctList])
    }
    function cancelEdit(punctId) {
        let newPunctList = [...punctList]
        for (let i = 0; i < newPunctList.length; i++) {
            if (newPunctList[i].id === punctId) {
                newPunctList[i].title = prevEditPunctText
            }
        }
        setCurrentEditPunctId(0)
        setPunctList([...newPunctList])
    }
    function submitEditPodPunct(podPunctId) {
        let newPunctList = [...punctList]
        for (let i = 0; i < newPunctList.length; i++) {
            for (let j = 0; j < newPunctList[i].items.length; j++) {
                if (newPunctList[i].items[j].id === podPunctId) {
                    newPunctList[i].items[j].title = currentEditPunctText
                }
            }
        }
        setCurrentEditPunctId(0)
        setPunctList([...newPunctList])
    }
    function cancelEditPodPunct(podPunctId) {
        let newPunctList = [...punctList]
        for (let i = 0; i < newPunctList.length; i++) {
            for (let j = 0; j < newPunctList[i].items.length; j++) {
                if (newPunctList[i].items[j].id === podPunctId) {
                    newPunctList[i].items[j].title = prevEditPunctText
                }
            }
        }
        setCurrentEditPunctId(0)
        setPunctList([...newPunctList])
    }
    function dragOverHendler(e) {
        if (isDragMain === 1) {
            e.preventDefault()
            e.target.style.backgroundColor = "var(--background1)"
        }
    }
    function dragEndHendler(e) {
        e.target.style.backgroundColor = "transparent"
    }
    function dragOverHendler2(e) {
        if (isDragMain === 2) {
            e.preventDefault()
            e.target.style.backgroundColor = "var(--background1)"
        }
    }
    function dragEndHendler2(e) {
        e.target.style.backgroundColor = "transparent"
    }
    function dragStartHendler(e, listElement) {
        setIsDragMain(1)
        setCurrentDragEl(listElement)
    }
    function dragStartHendler2(e, plid, listElement) {
        setIsDragMain(2)
        setDropParentId(plid)
        setCurrentDragEl(listElement)
    }
    function dropHendler(e, listElement) {
        if (isDragMain === 1) {
            e.preventDefault()
            e.target.style.backgroundColor = "transparent"
            let newPunctList = []
            let p = 0
            for (let i = 0; i < punctList.length; i++) {
                if (punctList[i].id !== currentDragEl.id) {
                    newPunctList.push(punctList[i])
                }
                if (punctList[i].id === listElement.id) {
                    p = i
                }
            }
            newPunctList.splice(p, 0, currentDragEl)
            let newL = calcNewIds(newPunctList)
            setIsDragMain(0)
            setPunctList([...newL])
        }
    }
    function dropHendler2(e, plid, listElement) {
        if (isDragMain === 2) {
            e.preventDefault()
            e.target.style.backgroundColor = "transparent"
            let newPunctList = [...punctList]
            let p = -1
            var c = false
            for (let i = 0; i < punctList.length; i++) {
                let newPodPunctList = []
                for (let j = 0; j < punctList[i].items.length; j++) {
                    if (punctList[i].items[j].id !== currentDragEl.id) {
                        newPodPunctList.push(punctList[i].items[j])
                    }
                    if (punctList[i].items[j].id === listElement) {
                        p = j
                        c = true
                    }
                }
                if (c) {
                    newPodPunctList.splice(p, 0, currentDragEl)
                    newPunctList[i].items = newPodPunctList
                    c = false
                }
            }
            console.log(newPunctList);
            let newL = calcNewIds(newPunctList)
            setIsDragMain(0)
            setPunctList([...newL])
            if(plid !== dropParentId) {
                deletePodPunct(dropParentId, currentDragEl.id)
            }
        }
    }
    return (
        <div className={active ? "modalContainer active" : "modalContainer"} onClick={() => setActive(false)}>
            <div className="modalContent" onClick={e => e.stopPropagation()}>
                <h2>  </h2>
                <hr></hr>
                <div>
                    <input type="text" placeholder="   " style={{ marginTop: "20px", display: "inline-block", width: "84%" }} />
                    <button className="buttonFill" style={{ width: "15%", marginLeft: "1%", backgroundColor: "var(--accentMain)" }}></button>
                </div>
                <div>
                    <h3 style={{ display: "inline-block", width: "84%" }}>  python</h3>
                    <button className="buttonWithoutFill" style={{ width: "15%", marginLeft: "1%" }} onClick={() => addPunct()}> </button>
                </div>
                <div className="punctList">
                    {punctList.map(pl =>
                        <div>
                            <div className="punctHeaderGroup">
                                {currentEditPunctId !== pl.id ?
                                    <div>
                                        <p className="punctHeader" draggable={true} style={{ cursor: "grab" }}
                                            onDragStart={(e) => dragStartHendler(e, pl)}
                                            onDragLeave={(e) => dragEndHendler(e)}
                                            onDragEnd={(e) => dragEndHendler(e)}
                                            onDragOver={(e) => dragOverHendler(e)}
                                            onDrop={(e) => dropHendler(e, pl)}>{pl.id}. {pl.title}</p>
                                        <button onClick={() => editPunct(pl.id, pl.title)}><svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 512 512"><path d="M362.7 19.3L314.3 67.7 444.3 197.7l48.4-48.4c25-25 25-65.5 0-90.5L453.3 19.3c-25-25-65.5-25-90.5 0zm-71 71L58.6 323.5c-10.4 10.4-18 23.3-22.2 37.4L1 481.2C-1.5 489.7 .8 498.8 7 505s15.3 8.5 23.7 6.1l120.3-35.4c14.1-4.2 27-11.8 37.4-22.2L421.7 220.3 291.7 90.3z" /></svg></button>
                                        <button onClick={() => deletePunct(pl.id)}><svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 512 512"><path d="M135.2 17.7L128 32H32C14.3 32 0 46.3 0 64S14.3 96 32 96H416c17.7 0 32-14.3 32-32s-14.3-32-32-32H320l-7.2-14.3C307.4 6.8 296.3 0 284.2 0H163.8c-12.1 0-23.2 6.8-28.6 17.7zM416 128H32L53.2 467c1.6 25.3 22.6 45 47.9 45H346.9c25.3 0 46.3-19.7 47.9-45L416 128z" /></svg></button>
                                        <button onClick={() => addPodPunct(pl.id)}><svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 448 512"><path d="M256 80c0-17.7-14.3-32-32-32s-32 14.3-32 32V224H48c-17.7 0-32 14.3-32 32s14.3 32 32 32H192V432c0 17.7 14.3 32 32 32s32-14.3 32-32V288H400c17.7 0 32-14.3 32-32s-14.3-32-32-32H256V80z" /></svg></button>
                                    </div> :
                                    <div>
                                        <input type="text" className="editPunctInput" defaultValue={currentEditPunctText} onChange={handleChange} />
                                        <button onClick={() => submitEdit(pl.id)}><svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 448 512"><path d="M438.6 105.4c12.5 12.5 12.5 32.8 0 45.3l-256 256c-12.5 12.5-32.8 12.5-45.3 0l-128-128c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0L160 338.7 393.4 105.4c12.5-12.5 32.8-12.5 45.3 0z" /></svg></button>
                                        <button onClick={() => cancelEdit(pl.id)}><svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 384 512"><path d="M342.6 150.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0L192 210.7 86.6 105.4c-12.5-12.5-32.8-12.5-45.3 0s-12.5 32.8 0 45.3L146.7 256 41.4 361.4c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0L192 301.3 297.4 406.6c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L237.3 256 342.6 150.6z" /></svg></button>
                                    </div>
                                }
                            </div>
                            <hr className="hiddenHR1" />
                            {pl.items.map(item =>
                                <div className="podpunctList">
                                    <div className="podpunctHeaderGroup" draggable={true} style={{ cursor: "grab" }}
                                        onDragStart={(e) => dragStartHendler2(e, pl.id, item)}
                                        onDragLeave={(e) => dragEndHendler2(e)}
                                        onDragEnd={(e) => dragEndHendler2(e)}
                                        onDragOver={(e) => dragOverHendler2(e)}
                                        onDrop={(e) => dropHendler2(e, pl.id, item.id)}
                                    >
                                        {currentEditPunctId !== item.id ?
                                            <div>
                                                <p className="podpunctHeader">{item.id} {item.title}</p>
                                                <button onClick={() => editPunct(item.id, item.title)}><svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 512 512"><path d="M362.7 19.3L314.3 67.7 444.3 197.7l48.4-48.4c25-25 25-65.5 0-90.5L453.3 19.3c-25-25-65.5-25-90.5 0zm-71 71L58.6 323.5c-10.4 10.4-18 23.3-22.2 37.4L1 481.2C-1.5 489.7 .8 498.8 7 505s15.3 8.5 23.7 6.1l120.3-35.4c14.1-4.2 27-11.8 37.4-22.2L421.7 220.3 291.7 90.3z" /></svg></button>
                                                <button onClick={() => deletePodPunct(pl.id, item.id)}><svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 448 512"><path d="M135.2 17.7L128 32H32C14.3 32 0 46.3 0 64S14.3 96 32 96H416c17.7 0 32-14.3 32-32s-14.3-32-32-32H320l-7.2-14.3C307.4 6.8 296.3 0 284.2 0H163.8c-12.1 0-23.2 6.8-28.6 17.7zM416 128H32L53.2 467c1.6 25.3 22.6 45 47.9 45H346.9c25.3 0 46.3-19.7 47.9-45L416 128z" /></svg></button>
                                            </div> :
                                            <div>
                                                <input type="text" className="editPunctInput" defaultValue={currentEditPunctText} onChange={handleChange} />
                                                <button onClick={() => submitEditPodPunct(item.id)}><svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 448 512"><path d="M438.6 105.4c12.5 12.5 12.5 32.8 0 45.3l-256 256c-12.5 12.5-32.8 12.5-45.3 0l-128-128c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0L160 338.7 393.4 105.4c12.5-12.5 32.8-12.5 45.3 0z" /></svg></button>
                                                <button onClick={() => cancelEditPodPunct(item.id)}><svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 384 512"><path d="M342.6 150.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0L192 210.7 86.6 105.4c-12.5-12.5-32.8-12.5-45.3 0s-12.5 32.8 0 45.3L146.7 256 41.4 361.4c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0L192 301.3 297.4 406.6c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L237.3 256 342.6 150.6z" /></svg></button>
                                            </div>
                                        }
                                    </div>
                                    <hr className="hiddenHR2" />
                                </div>
                            )}
                        </div>
                    )}
                </div>
                <hr style={{ marginTop: "20px" }}></hr>
                <div className="navigationTableGroup">
                    <button type="button" style={{ marginRight: "10px" }} className="buttonFillNavWF"></button>
                    <button type="button" className="buttonFillNav" style={{ marginRight: "10px" }}></button>
                </div>
            </div>
        </div>
    )
}
export default CreateModal