import { useState } from "react"

const UsersModal = ({ active, setActive }) => {
    return (
        <div className={active ? "modalContainer active" : "modalContainer"} onClick={() => setActive(false)}>
            <div className="modalContent" onClick={e => e.stopPropagation()}>
                <h2> </h2>
                <hr></hr>
                <div className="row">
                    <div className="col-12">
                        <p></p>
                        <input type='text' />
                    </div>
                    <div className="col-md-6 col-12">
                        <p></p>
                        <input type='text' />
                    </div>
                    <div className="col-md-6 col-12">
                        <p></p>
                        <input type='text' />
                    </div>
                    <div className="col-md-4 col-12">
                        <p></p>
                        <select>
                            <option value="-1" ></option>
                            <option value="2"> </option>
                        </select>
                    </div>
                    <div className="col-md-4 col-12">
                        <p></p>
                        <input type='password' />
                    </div>
                    <div className="col-md-4 col-12">
                        <p> </p>
                        <input type='password' />
                    </div>
                </div>

                <hr style={{ marginTop: "20px" }}></hr>
                <div className="navigationTableGroup">
                    <button type="button" style={{ marginRight: "10px" }} className="buttonWithoutFill"></button>
                    <button type="button" className="buttonFill" style={{ marginRight: "10px" }}></button>
                </div>
            </div>
        </div>
    )
}
export default UsersModal