import { useState } from "react"
import UsersTable from "./UsersTable"
import WorkspaceFiles from "./WorkspaceFiles"

const WorkspaceModal = ({ active, setActive }) => {
    const [openTabNum, setOpenTabNum] = useState(1)
    return (
        <div className={active ? "modalContainer active" : "modalContainer"} onClick={() => setActive(false)}>
            <div className="modalContent" onClick={e => e.stopPropagation()}>
                <h2> </h2>
                <hr></hr>
                <div className="row">
                    <div className="col-auto" onClick={() => setOpenTabNum(1)} style={openTabNum === 1 ? { cursor: "pointer", color: "var(--accentMain)" } : { cursor: "pointer" }}>
                        <p> </p>
                        <hr style={openTabNum === 1 ? { borderColor: "var(--accentMain)" } : {}}></hr>
                    </div>
                    <div className="col-auto" onClick={() => setOpenTabNum(2)} style={openTabNum === 2 ? { cursor: "pointer", color: "var(--accentMain)" } : { cursor: "pointer" }}>
                        <p> </p>
                        <hr style={openTabNum === 2 ? { borderColor: "var(--accentMain)" } : {}}></hr>
                    </div>
                    <div className="col-auto" onClick={() => setOpenTabNum(3)} style={openTabNum === 3 ? { cursor: "pointer", color: "var(--accentMain)" } : { cursor: "pointer" }}>
                        <p> </p>
                        <hr style={openTabNum === 3 ? { borderColor: "var(--accentMain)" } : {}}></hr>
                    </div>
                </div>
                {
                    openTabNum === 1 &&
                    <div className="row">
                        <div className="col-12">
                            <p>  </p>
                            <input type='text' />
                        </div>
                        <div className="col-md-6 col-12">
                            <p> </p>
                            <input type='text' />
                        </div>
                        <div className="col-md-6 col-12">
                            <p></p>
                            <select>
                                <option value="-1" ></option>
                                <option value="2"></option>
                                <option value="3"></option>
                            </select>
                        </div>
                        <div className="col-md-12 col-12">
                            <p></p>
                            <textarea></textarea>
                        </div>
                    </div>
                }
                {
                    openTabNum === 2 &&
                    <UsersTable />
                }
                {
                    openTabNum === 3 &&
                    <WorkspaceFiles />
                }
                <hr style={{marginTop: "20px"}}></hr>
                <div className="navigationTableGroup">
                    <button type="button" style={{ marginRight: "10px" }} className="buttonWithoutFill"></button>
                    <button type="button" className="buttonFill" style={{ marginRight: "10px" }}></button>
                </div>
            </div>
        </div>
    )
}
export default WorkspaceModal