import { useState } from "react"
import CreateModal from "./CreateModal";
function GenerationPage() {
    const [createModalActive, setCreateModalActive] = useState(false);
    return (
        <div className='content'>
            <div className='contentData' style={{ marginTop: "65px" }}>
                <div>
                    <div className="row" style={{ color: "var(--textColor)", }}>
                        <div className="col-auto mobW">
                            <button className="buttonFill" onClick={() => setCreateModalActive(true)}> </button>
                        </div>
                        <div className="col-auto mobW">
                            <button className="buttonWithoutFill"> </button>
                        </div>
                    </div>
                </div>
                <CreateModal active={createModalActive} setActive={setCreateModalActive}/>
            </div>
        </div>
    );
}

export default GenerationPage;