import UsersTable from "./UsersTable";
import UsersModal from "./UsersModal";
import UsersModalLoadCSV from "./UserModalLoadCSV";
import { useState } from "react"
function UsersPage() {
  const [usersModalActive, setUsersModalActive] = useState(false);
  const [usersModalLoadCSVActive, setUsersModalLoadCSVActive] = useState(false);
    return (
      <div className='content'>
            <div className='contentData' style={{ marginTop: "70px" }}>
                <div>
                    <div className="row">
                        <div className="col-auto mobW">
                            <input type="button" className="buttonFill" value=" " onClick={() => setUsersModalActive(true)}/>
                        </div>
                        <div className="col-auto mobW">
                            <input type="button" className="buttonWithoutFill" value="   " onClick={() => setUsersModalLoadCSVActive(true)}/>
                        </div>
                    </div>
                </div>
                <UsersTable/>
                <UsersModal active={usersModalActive} setActive={setUsersModalActive}/>
                <UsersModalLoadCSV active={usersModalLoadCSVActive} setActive={setUsersModalLoadCSVActive}/>
            </div>
        </div>
    );
  }
  
  export default UsersPage;