import './assets/App.css';
import './assets/bootstrap-grid.css'
import Header from './Header';
import Sidebar from './Sidebar';
import Workspace from './Workspace/Workspace'
import UsersPage from './Users/Users';
import AnalizePage from './Analize/Analize';
import LoginPage from './Login';
import GenerationPage from './Generation/Generation';
import { useState } from "react"
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

function App() {
  const [openPageNum, setOpenPageNum] = useState(1)
  return (
    <div>
      <Router>
        <Routes>
          <Route path="/login" element={<LoginPage />} />
          <Route path="/" element={
            <div>
              <Header />
              <Sidebar pageNum={openPageNum} setPageNum={setOpenPageNum} />
              {openPageNum === 1 && <Workspace />}
              {openPageNum === 2 && <UsersPage />}
              {openPageNum === 3 && <AnalizePage />}
              {openPageNum === 4 && <GenerationPage />}
            </div>
          } />
        </Routes>
      </Router>
    </div>
  );
}

export default App;
