
import { Tooltip } from 'react-tooltip'
import { useState } from 'react';

function Header() {
    const [isDarkMode, setIsDarkMode] = useState(true);
    function changeTheme() {
        if(isDarkMode) {
            setIsDarkMode(false)
            document.documentElement.style.setProperty('--background2',"#fff");
            document.documentElement.style.setProperty('--textColor',"#000");
            document.documentElement.style.setProperty('--accentMain',"#0026a3");
            document.documentElement.style.setProperty('--background1',"#f0efeb");
            document.documentElement.style.setProperty('--background3',"#f8f7ff");
            document.documentElement.style.setProperty('--accentMainHover',"#2548be");
        }
        else {
            setIsDarkMode(true)
            document.documentElement.style.setProperty('--background2',"#292929");
            document.documentElement.style.setProperty('--textColor',"#fff");
            document.documentElement.style.setProperty('--accentMain',"#ffa31a");
            document.documentElement.style.setProperty('--background1',"#808080");
            document.documentElement.style.setProperty('--background3',"#1b1b1b");
            document.documentElement.style.setProperty('--accentMainHover',"#ffba53");
        }
    }
    var tooltipTheme = isDarkMode ? "dark" : "dark";
    return (
        <div>
            <nav className="header">
                <h1>OOO ""</h1>
                <div className='toggle-switch'>
                    <label>
                        <input type='checkbox' onChange={changeTheme}/>
                        <span className='slider'></span>
                    </label>
                </div>
            </nav>
            <Tooltip id="my-tooltip" variant={tooltipTheme} delayShow="0" float="true" style={{backgroundColor: "var(--background3Glass)", backdropFilter: "blur(6px)", WebkitBackdropFilter: "blur(9.2px)", zIndex: "1000"}}/>
        </div>
    );
}

export default Header;
