import WorkspaceTable from './WorkspaceTable';
import WorkspaceModal from './WorkspaceModal';
import WorkspaceModalAI from './WorkspaceModalAI';
import { useState } from 'react';

function WorkspacePage() {
    const [workspaceModalActive, setWorkspaceModalActive] = useState(false);
    const [workspaceModalAIActive, setWorkspaceModalAIActive] = useState(0);
    return (
        <div className='content'>
            <div className='contentData'>
                <div className="row workspaceMainInfo">
                    <div className="col-auto">
                        <h2 id="parentWSName">Root</h2>
                        <hr />
                    </div>
                    <div className="col-auto" >
                        <div data-tooltip-id="my-tooltip" data-tooltip-content="" data-tooltip-place="right">
                            <h2>59bb77dc953ee7a7ec2d</h2>
                        </div>
                    </div>
                </div>
                <hr style={{ borderColor: 'var(--accentMain)' }} />
                <div style={{ marginTop: "20px" }}>
                    <div className="row">
                        <div className="col-auto mobW">
                            <input type="button" className="buttonFill" value=" " onClick={() => setWorkspaceModalActive(true)}/>
                        </div>
                        <div className="col-auto mobW">
                            <input type="button" className="buttonFill" value=" " />
                        </div>
                        <div className="col-auto mobW">
                            <input type="button" className="buttonWithoutFill" value="  " onClick={() => setWorkspaceModalAIActive(1)}/>
                        </div>
                        <div className="col-auto mobW">
                            <input type="button" className="buttonWithoutFill" value=" " onClick={() => setWorkspaceModalAIActive(2)}/>
                        </div>
                        <div className="col-auto mobW">
                            <input type="button" className="buttonWithoutFill" value=" " onClick={() => setWorkspaceModalAIActive(3)}/>
                        </div>
                        <div className="col-auto mobW">
                            <input type="button" className="buttonWithoutFill" value=" " onClick={() => setWorkspaceModalAIActive(4)}/>
                        </div>
                    </div>
                </div>
                <div className='tableNavigationLine'>
                    <p id="wsPathLine">.../root/  Python</p>
                </div>
                <WorkspaceTable />
                <WorkspaceModal active={workspaceModalActive} setActive={setWorkspaceModalActive}/>
                <WorkspaceModalAI active={workspaceModalAIActive} setActive={setWorkspaceModalAIActive}/>
            </div>
        </div>
    );
}

export default WorkspacePage;
